'use client';

import { Box, Typography, LinearProgress, Paper, useTheme } from '@mui/material';

export default function DSAProgressTracker({ progress }) {
  const theme = useTheme();
  
  // Calculate overall progress
  const calculateProgress = () => {
    let totalProblems = 0;
    let completedProblems = 0;
    
    Object.values(progress).forEach(chapter => {
      Object.values(chapter).forEach(isCompleted => {
        totalProblems++;
        if (isCompleted) completedProblems++;
      });
    });
    
    return {
      total: totalProblems,
      completed: completedProblems,
      percentage: totalProblems > 0 ? Math.round((completedProblems / totalProblems) * 100) : 0
    };
  };
  
  const { total, completed, percentage } = calculateProgress();

  return (
    <Paper 
      elevation={0} 
      sx={{ 
        p: 3, 
        backgroundColor: 'background.paper',
        borderRadius: 2,
        position: 'sticky',
        top: 20
      }}
    >
      <Typography variant="h6" sx={{ mb: 2, fontWeight: 600 }}>Progress Tracker</Typography>
      
      <Box sx={{ mb: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
          <Typography variant="body2" color="text.secondary">
            Overall Progress
          </Typography>
          <Typography variant="body2" color="primary" fontWeight={600}>
            {percentage}%
          </Typography>
        </Box>
        <LinearProgress 
          variant="determinate" 
          value={percentage} 
          sx={{ 
            height: 8, 
            borderRadius: 4,
            backgroundColor: theme.palette.grey[200],
            '& .MuiLinearProgress-bar': {
              borderRadius: 4,
            }
          }} 
        />
        <Typography variant="caption" color="text.secondary" sx={{ mt: 1, display: 'block' }}>
          {completed} of {total} problems completed
        </Typography>
      </Box>

      <Box>
        <Typography variant="subtitle2" color="text.secondary" sx={{ mb: 1 }}>
          Quick Actions
        </Typography>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1 }}>
          <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Box 
              component="span" 
              sx={{ 
                width: 12, 
                height: 12, 
                borderRadius: '50%', 
                bgcolor: 'success.main' 
              }} 
            />
            Easy: {Math.round(Math.random() * 20)}/50
          </Typography>
          <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Box 
              component="span" 
              sx={{ 
                width: 12, 
                height: 12, 
                borderRadius: '50%', 
                bgcolor: 'warning.main' 
              }} 
            />
            Medium: {Math.round(Math.random() * 30)}/100
          </Typography>
          <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Box 
              component="span" 
              sx={{ 
                width: 12, 
                height: 12, 
                borderRadius: '50%', 
                bgcolor: 'error.main' 
              }} 
            />
            Hard: {Math.round(Math.random() * 20)}/50
          </Typography>
        </Box>
      </Box>
    </Paper>
  );
}